import React from "react";
import styled from "styled-components";

import { fonts } from '../../utils/Fonts';

export default function CreatorsCard({ banner, profile, name, role, children }) {
  return (
    <Card>
      <div className="head">
        <Profile src={profile}/>
        <Banner banner={banner}/>
      </div>
      <div className="content">
        <h1>{ name }</h1>
        <p>{ role }</p>
        <SocialMedia>
          {children}
        </SocialMedia>
      </div>
    </Card>
  )
}

const Banner = styled.div`
    clip-path: inset(0, 50%);
    background-image: url(${({banner}) => banner});
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    /* esse 4em vem do padding do bottom, para ele ficar bem no meio do banner */
    height: calc(100% - 4em);
    margin-bottom: auto;
  `
const Profile = styled.img`
  position: absolute;
  height: 80%;
  border-radius: 50%;
  /* padding: 10vw; */
  bottom: 0;
`


