import styled from "styled-components";

import { theme } from "./utils/Theme";

export const BotaoSubscribe = styled.button`
  cursor: pointer;
  border: 1px solid ${theme.default};
  padding: 0.5em 2em;
  background-color: ${theme.background1};
  color: ${theme.neutral0};
  height: 100%;
  transition: all .2s ease-in-out;

  &:hover{
    background-color: rgb(255, 216, 133);
    color: rgb(31, 30, 29);
  }

  ::before {
    content: "Subscribe";
  }
`;

export const NavBarWrapper = styled.nav`
  border: 2px solid rgba(92, 90, 87, 0.29);
  padding: 32px 32px 10px 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 999;
  right: 0;

  * {
    /* estava tendo problemas de um span nao pegando essa fonte */
    font-family: "Epilogue";
  }

  > img {
    z-index: -1;
  }

  > ${BotaoSubscribe} {
    display: none;
  }

  @media screen and (min-width: 768px) {
    padding: 10px 88px;
    border-bottom: 1px solid ${theme.neutral80};
    padding-bottom: 1em;

    > img {
      z-index: 1;
    }

    > ${BotaoSubscribe} {
      display: block;
      z-index: 1;
    }
  }
`;

const Barra = styled.div`
  width: ${({ visivel }) =>
    visivel
      ? "calc(calc(calc(calc(100vw * 2) + calc(100vh * 2)) * 0.5) / 2.5)"
      : "0px"};
  height: 500vw;
  transition: width 1s;
  background-color: ${theme.neutral80};
  display: block;
  position: absolute;
  transform-origin: center;
  z-index: -1;

  @media screen and (min-width: 768px) {
    display: none;
  }
`;

export const BarraTop = styled(Barra)`
  top: 0;
  left: 0;
  transform: translate(-50%, -50%) rotateZ(45deg);
`;

export const BarraMeio = styled(Barra)`
  bottom: 50%;
  right: 50%;
  transform: translate(50%, 50%) rotateZ(45deg);
`;

export const BarraBotton = styled(Barra)`
  bottom: 0;
  right: 0;
  transform: translate(50%, 50%) rotateZ(45deg);
`;

export const Conteudo = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: baseline;

  height: 100vh;
  width: 100vw;

  margin-left: 1em;

  transition: opacity 0.5s 0.1s, transform 0.5s 0.1s;

  transform: ${({ visivel }) =>
    visivel ? "translateY(0%)" : "translateY(-10%)"};
  opacity: ${({ visivel }) => (visivel ? "1" : "0")};

  > div {
    display: flex;
    flex-direction: column;

    > * {
      padding: 0.5em;
      color: ${theme.neutral0};
      text-decoration: none;
    }
  }

  @media screen and (min-width: 768px) {
    flex-direction: row;
    transform: none;
    visibility: visible;
    opacity: 1;
    height: auto;
    width: auto;

    margin: 0;

    > div {
      flex-direction: row;
      align-items: center;

      > ${BotaoSubscribe} {
        display: none;
      }
    }
  }
`;

export const MenuMobile = styled.div`
  visibility: ${({ visivel }) =>
    visivel ? "visible" : "hidden; transition: visibility 0s 1.1s"};
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  width: 100vw;
  height: 100vh;

  overflow: hidden;

  @media screen and (min-width: 768px) {
    position: static;
    display: flex;
    align-items: center;
    width: initial;
    height: auto;
  }
`;

export const AbrirMenu = styled.div`
  position: relative;
  z-index: 1;

  @media screen and (min-width: 768px) {
    display: none;
  }

  svg {
    color: ${theme.neutral0};
  }

  .menu {
    margin-left: auto;
    background-color: transparent;
    border: none;
    cursor: pointer;
    display: flex;
    padding: 0;
  }

  .menu > svg {
    width: 50px;
    height: 50px;
  }
  .line {
    fill: none;
    stroke: ${theme.neutral0};
    stroke-width: 6;
    transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
      stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  .line1 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
  }
  .line2 {
    stroke-dasharray: 60 60;
    stroke-width: 6;
  }
  .line3 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
  }
  .opened .line1 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
  }
  .opened .line2 {
    stroke-dasharray: 1 60;
    stroke-dashoffset: -30;
    stroke-width: 6;
  }
  .opened .line3 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
  }
`;
