import React, {useState} from 'react'

import {Link} from 'react-router-dom'

import styled from 'styled-components'
import {AiOutlineDown} from 'react-icons/ai'
import {BsFillCircleFill} from 'react-icons/bs'

import { theme } from './utils/Theme'

export default function DropDownItem({nome, itens, pageChange}) {
    const [caido, setCaido] = useState(false)

    function handleClick() {
        setCaido(false)
        pageChange()
    }

    return (
        <NavBarWrapper caido={caido}>
            <button onClick={() => setCaido(!caido)}><span>{nome}</span><AiOutlineDown/></button>
            <LinksDentro caido={caido}>
                {itens.map(item => 
                    <>
                    <hr />
                    <div key={item[0]}>
                        <BsFillCircleFill/>
                        <Link to={{pathname:item[1]}} onClick={pageChange}>{item[0]}</Link>
                    </div>
                    </>
                )}
            </LinksDentro>
        </NavBarWrapper>
    )
}

const SetinhaPraCima = styled.div`
    background-color: tomato;
    width: 100%;
    padding: 10%;
`

const NavBarWrapper = styled.span`
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: baseline;


    > button svg {
        margin-left: .5em;
        font-size: .6em;

        transform: rotate(${({caido}) => caido ? '.5turn' : '0'});

        transition: transform .4s;
    }

    > button {
        position: relative;
        z-index: 2;
        
        font-size: 1em;
        
        color: ${({caido}) => caido ? theme.lightYellow : theme.neutral0};
        background: transparent;
        border: none;
        cursor: pointer;
    }

    
    @media screen and (min-width: 768px) {
        position: static;
        align-items: center;
        justify-content: center;
    }
    `

const LinksDentro = styled.div`
    display: flex;
    flex-direction: column;
    hr {display: none}

    > div {
        display: flex;
        flex-direction: row;
        align-items: center;
        
        > svg {
            font-size: .2em;
            margin: 0 1rem;
        }
    }

    > div {
        display: flex;
        align-items: center;
        position: relative;
        z-index: 1;

        /* nao me orgulho disso, mas nao tenho tempo pra consertar */
        visibility: ${({caido}) => caido ? 'visible' : 'hidden'};
        height : ${({caido}) => caido ? '2em' : '0'};
        transform: translateY(${({caido}) => caido ? '0' : '-em'});
        opacity: ${({caido}) => caido ? '1' : '0'};

        transition: height .5s, transform .5s, visibility .5s, opacity .3s .1s;

        > a {text-decoration: none;color: ${theme.neutral0};}
    }

    @media screen and (min-width: 768px){
        position: absolute;
        z-index: 9;

        opacity: ${({caido}) => caido ? '1' : '0'};
        transition: opacity .5s;

        clip-path: polygon(50% 0%,53% 6%,100% 6%,100% 100%,0 100%,0 6%,47% 6%);
        
        hr {
            display: block;
            margin: .5em 0;
        }

        hr:nth-child(1) {
            display: none;
        }
        
        padding: 1em;
        padding-top: 1.1em;
        background-color: ${theme.background1};
        box-shadow: inset 0 0 5em 0em #bbb3, 0 0 10px 10px #0003;
        min-width: 12em;
        top: 50%;

        svg { display:none }
    }
`