import React from 'react';
import styled from 'styled-components';

import { Title, Content } from './Content.style';

export default ({ MobileIMG, DesktopIMG, title, text }) => {
  return (
    <Content>
      <Image MobileIMG={MobileIMG} DesktopIMG={DesktopIMG}></Image>
      <Title>
        <h1>{title}</h1>
        <p>{text}</p>
      </Title>
    </Content>
  )
}

const Image = styled.div`
  margin: 24px auto;
  background-image: url(${({MobileIMG}) => MobileIMG});
  background-repeat: no-repeat;
  background-size: cover;
  width: 90%;
  height: 375px;
@media(min-width: 1024px) {
  margin: 0 0 0 5px;
  width: auto;
  background-image: url(${({DesktopIMG}) => DesktopIMG});
  background-repeat: no-repeat;
  background-size: cover;
}
`