import { useState } from "react";

import {createGlobalStyle} from 'styled-components'
import {Link} from 'react-router-dom'

import Logo from '../img/Logo.png'

import { 
    NavBarWrapper,
    AbrirMenu,   
    MenuMobile, 
    Conteudo, 
    BarraTop, 
    BarraBotton, 
    BarraMeio,
    BotaoSubscribe
} from './NavBar.style'

import DropDownItem from './DropDownItem';
import Popup from "./pages/Popup";

export default function NavBar() {
  const [menuAberto, setMenuAberto] = useState(false);

  const [mostrarPopup, setMostrarPopup] = useState(false)

  const handleAbrirMenuClick = () => {
    setMenuAberto(!menuAberto)
    window.scrollTo(0, 0)
  }

  const handlePopupClick = () => {
      setMostrarPopup(!mostrarPopup)
  }

  //para fechar o menuMobile quando a pagina mudar
  function handlePageChange(event) {
      console.log('oi')
    setMenuAberto(false);
  }

  return (
    <NavBarWrapper>
        {menuAberto ? <PararScroll/> : null}
        <img src={Logo}/>
        <MenuMobile visivel={menuAberto}>
            <Conteudo visivel={menuAberto}>
                <div>
                    <a href="#" onClick={handlePageChange}>Farm</a>
                    <DropDownItem 
                        nome='Battle Center'
                        itens={[
                            ['Battle Free', 'google.com'],
                            ['Battle Ultra', 'bing.com']
                        ]}
                        pageChange={handlePageChange}
                    />
                    <DropDownItem 
                        nome='Marktplace'
                        itens={[
                            ['Buy packs', 'google.com'],
                            ['Buy and Sell', 'bing.com']
                        ]}
                        pageChange={handlePageChange}
                    />
                    <Link to='/ccctoken' onClick={handlePageChange}>CCC token</Link>
                    <DropDownItem 
                        nome='Know'
                        itens={[
                            ['Cards', 'google.com'],
                            ['Blog', 'bing.com'],
                            ['Whitepaper', 'bing.com'],
                        ]}
                        pageChange={handlePageChange}    
                    />
                    <BotaoSubscribe onClick ={handlePopupClick}/>
                    {mostrarPopup ? <Popup close = {handlePopupClick} /> : null}
                </div>
            </Conteudo>
            <div>
                <BarraTop visivel={menuAberto} />
                <BarraMeio visivel={menuAberto} />
                <BarraBotton visivel={menuAberto} />
            </div>
        </MenuMobile>
        <BotaoSubscribe onClick ={handlePopupClick}/>
        {mostrarPopup ? <Popup close = {handlePopupClick} /> : null}
        <AbrirMenu>
            <button
                className={`menu ${menuAberto ? "opened" : ""}`}
                onClick={handleAbrirMenuClick}
            >
                <svg viewBox="0 0 100 100">
                <path
                    className="line line1"
                    d="M 20,29.000046 H 80.000231 C 80.000231,29.000046 94.498839,28.817352 94.532987,66.711331 94.543142,77.980673 90.966081,81.670246 85.259173,81.668997 79.552261,81.667751 75.000211,74.999942 75.000211,74.999942 L 25.000021,25.000058"
                />
                <path className="line line2" d="M 20,50 H 80" />
                <path
                    className="line line3"
                    d="M 20,70.999954 H 80.000231 C 80.000231,70.999954 94.498839,71.182648 94.532987,33.288669 94.543142,22.019327 90.966081,18.329754 85.259173,18.331003 79.552261,18.332249 75.000211,25.000058 75.000211,25.000058 L 25.000021,74.999942"
                />
                </svg>
            </button>
        </AbrirMenu>
    </NavBarWrapper>
  );
}

const PararScroll = createGlobalStyle`
    body {overflow-y: hidden;height: 100vh;}
`