import React, {useState} from 'react'
import styled from 'styled-components'

import {
    AiOutlineLeft,
    AiOutlineRight
} from 'react-icons/ai'

import ComponentSlider from './ComponentSlider'
import CardComponent from './CardComponent'

import { theme } from './utils/Theme'

import { cartaFirstDefense,cartaAiron,cartaBookOfPower,cartaNigromante } from './CardExclusiveData'
import BotaoEstilizado from './BotaoEstilizado.style'
import { fonts } from './utils/Fonts'

const CardExclusive = () => {
    const [pos, setPos] = useState(1)
    const quantidadeCartas = 4;

    return (
        <CardExclusiveWrapper>
            <Separator/>
            <Cabecalho>
                {/* esse nbsp e o codigo do espaco, e menos dificil de dar bug pq alguem removeu um espaco em branco*/}
                <p>CARDS&nbsp;<BGGradiente>EXCLUSIVE</BGGradiente></p>
                <div>
                    <BotaoEstilizado onClick={()=>setPos(Math.max(pos-1, 0))}><AiOutlineLeft /></BotaoEstilizado>
                    <BotaoEstilizado onClick={()=>setPos(Math.min(pos+1, quantidadeCartas - 1))}><AiOutlineRight /></BotaoEstilizado>
                </div>
            </Cabecalho>
            <ComponentSlider posicao={pos}>
                <CardComponent {...cartaFirstDefense} key={cartaFirstDefense.titulo}/>
                <CardComponent {...cartaNigromante} key={cartaNigromante.titulo}/>
                <CardComponent {...cartaBookOfPower} key={cartaBookOfPower.titulo}/>
                <CardComponent {...cartaAiron} key={cartaAiron.titulo}/>
            </ComponentSlider>
        </CardExclusiveWrapper>
    )
}

const Separator = styled.div`
    display: none;

    @media screen and (min-width: 768px){
        display: block;
        width: 35%;
        height: 100%;
        background: ${theme.background2};
        top: 0;
        right: 0;
        position: absolute;
        z-index: -1;
    }
`

const CardExclusiveWrapper = styled.section`
    position: relative;
    width: 100%;

    @media screen and (min-width: 768px){
        padding: 88px;
        > div {border: 1px solid ${theme.neutral60}}
    }
`

const Cabecalho = styled.h1`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 1rem;
    font-family: "termina", sans-serif;
    color: ${theme.neutral0};
    font-size: 2em;

    > p {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    button {
        display: none
    }

    @media screen and (min-width: 768px){
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        > p {
            display: inline;
        }

        > div {
            display : flex;
            flex-direction: row;

            > ${BotaoEstilizado} {display: inline;}
        }
    }
`

const BGGradiente = styled.span`
    background: ${theme.gradient};
    background-clip: text;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
    -webkit-text-fill-color: transparent;
`

export default CardExclusive