import React, { useState, useEffect, useRef } from 'react'
import styled from 'styled-components'

const ComponentSlider = ({className, posicao, children}) => {
    const sliderRef = useRef(null)
    const childrenArray = React.Children.toArray(children)

    useEffect(() => {
        const elementDentro = sliderRef.current.childNodes[0]
        const posicaoPixel = (elementDentro.clientWidth * posicao)
        console.log(posicaoPixel)
        sliderRef.current.scrollTo(posicaoPixel, 0)
    }, [posicao])

    return (
        <SliderWrapper ref={sliderRef}>
            {childrenArray.map(v => <SliderItemWrapper>{v}</SliderItemWrapper>)}
        </SliderWrapper>
    )
}

export const SliderWrapper = styled.div`
    display: flex;
    overflow-x: auto;

    height: 100%;
    width: 100%;

    ::-webkit-scrollbar{
        display: none;
    };
    -ms-overflow-style: none; 
    scrollbar-width: none;


    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
   --webkit-overflow-scrolling: touch;

   @media screen and (min-width: 768px){
        /* pointer-events : none; */
        height: 100%;
        width: 100%;
   }    
`


export const SliderItemWrapper = styled.div`
    width: 70%;
    flex: none;

    > * {
        height: 100%;
        width: 90%;
        object-fit: contain;
        scroll-snap-align: center;
    }

    @media screen and (min-width: 768px){
        width: 100%;
   }
`

export default ComponentSlider